import React from 'react'
import Link from 'gatsby-link'
import Navigoat from '../layouts/navigoat.js'
import ProjectPage from '../layouts/pr_page.js'

export default () =>
  <div>
    <Navigoat active_tab='projs'/>
    <ProjectPage title = 'GraSP: Graph Streaming Partitioner'
                 paper_title = 'GraSP: Distributed Streaming Graph Partitioning'
                 authors = 'Casey Battaglino, Robert Pienta, and Richard Vuduc'
                 abstract = 'This paper presents a distributed, streaming graph partitioner, Graph Streaming Partitioner (GraSP), which makes partition decisions as each vertex is read from memory, simulating an online algorithm that must process nodes as they arrive. GraSP is a lightweight high-performance computing (HPC) library implemented in MPI, designed to be easily substituted for existing HPC partitioners such as ParMETIS. It is the first MPI implementation for streaming partitioning of which we are aware, and is empirically orders-of-magnitude faster than existing partitioners while providing comparable partitioning quality. We demonstrate the scalability of GraSP on up to 1024 compute nodes of NERSCâ€™s Edison supercomputer. Given a minute of run-time, GraSP can partition a graph three orders of magnitude larger than ParMETIS can.'
                 paper_linky = '../data/15-kdd-grasp.pdf'
                 paper_img = '../imgs/projs/pr-grasp-cj.png'
                 img_caption = 'GraSP uses independent streams of nodes from the graph to heuristically partition massive graphs. Above two parallel restreaming steps are shown over four processes.'
                 video_linky = ''
    />

  </div>



